* {
  font-family: Arial, Helvetica, sans-serif;
}
body {
  padding: 60px;
}
input {
  width: 100%;
  font-size: 20px;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  overflow: hidden;
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
}
.card p {
  padding: 5px;
  text-align: center;
}
.card img {
  max-height: 160px;
}
